<template>
  <div>
    <el-dialog v-model="displayDialog" :title="theDialogTitle">
      <el-form label-width="150px">
        <el-form-item label="路径">
          <el-input v-model="item1.path">
            <template #append>
              <el-button @click="handleClickBrowse"> 浏览... </el-button>
            </template>
          </el-input>
          <div class="tip-bad-path" v-show="displayTipForBadPath">
            错误的路径！
          </div>
        </el-form-item>
        <el-form-item label="名称">
          <el-input v-model="item1.name"></el-input>
        </el-form-item>

        <el-form-item label="标题">
          <el-input v-model="item1.title"></el-input>
        </el-form-item>

        <el-form-item label="排列顺序">
          <!-- <el-input v-model="item1.open_with_priority"></el-input> -->
          <OpenWithPrioritySel v-model="item1.open_with_priority" />
        </el-form-item>

        <el-form-item label="说明"
          ><el-input type="textarea" v-model="item1.description"></el-input
        ></el-form-item>
      </el-form>

      <div>
        <el-button type="success" @click="handleClickOK">保存</el-button>
        <el-button type="default" @click="handleClickCancel">取消</el-button>
      </div>
    </el-dialog>

    <OpenFileDialog
      v-model="displayOpenFileDialog"
      @open-file="handleOpenExeFiles"
    />
  </div>
</template>

<script>
import OpenWithPrioritySel from "./open-with-priority-selector.vue";
import OpenFileDialog from "./../../filebrowser/dialog-open.vue";

export default {
  name: "settings-executables-item-add",

  components: { OpenWithPrioritySel, OpenFileDialog },

  computed: {
    isModeAdd() {
      let m = (this.method + "").toUpperCase();
      return m == "POST";
    },

    isModeEdit() {
      let m = (this.method + "").toUpperCase();
      return m == "PUT";
    },

    theDialogTitle() {
      if (this.isModeAdd) {
        return "添加应用";
      } else {
        return "修改应用";
      }
    },
  },

  data() {
    return {
      item1: {
        id: 0,
        name: "",
        title: "",
        path: "",
        description: "",
      },
      displayDialog: false,
      displayTipForBadPath: false,
      displayOpenFileDialog: false,
    };
  },

  methods: {
    handleClickCancel() {
      this.displayDialog = false;
    },

    handleClickOK() {
      let item = this.item1;
      let action = "executable/";
      if (this.isModeAdd) {
        action += "insert";
      } else if (this.isModeEdit) {
        action += "update";
      }
      this.$store.dispatch(action, item).then(() => {
        this.displayDialog = false;
      });
    },

    handleClickBrowse() {
      this.displayOpenFileDialog = true;
    },

    // todo ...
    handleOpenExeFiles(files) {
      if (files == null) {
        return;
      }
      let path = null;
      for (var i in files) {
        let f = files[i];
        path = f.path;
        break;
      }
      if (path == null) {
        return;
      }
      let item = this.item1;
      item.path = path;
      this.$store.dispatch("executable/prepareInsert", item).then((res) => {
        this.handleFindResult(res.data);
      });
    },

    handleFindResult(vo) {
      let list = vo.executables;
      let item = list[0];
      if (item.ready && item.size > 0 && item.sha256sum != "") {
        this.item1.name = item.name;
        this.item1.title = item.title;
        this.item1.path = item.path;
        this.displayTipForBadPath = false;
        return;
      }
      this.displayTipForBadPath = true;
    },
  },

  mounted() {},

  props: {
    enabled: Boolean,
    item: Object,
    method: String, // 'POST'表示insert  , 'PUT'表示update
  },

  watch: {
    displayDialog(value) {
      this.$emit("update:enabled", value);
    },

    enabled(value) {
      this.displayDialog = value;
    },

    item(value) {
      this.item1 = value;
    },
  },
};
</script>

<style scoped>
.tip-bad-path {
  color: red;
}
</style>
