<template>
  <div>
    <el-table :data="items">
      <el-table-column label="" width="48px">
        <template #default="scope">
          <img class="item-icon-img" :src="scope.row.icon" />
        </template>
      </el-table-column>

      <el-table-column label="名称" prop="name"></el-table-column>
      <el-table-column label="标题" prop="title"></el-table-column>
      <el-table-column label="状态" prop="state"></el-table-column>
      <el-table-column label="操作">
        <template #default="scope">
          <div class="operation-buttons">
            <el-button
              type="primary"
              link
              @click="handleClickItemDetail(scope.row)"
              >详情</el-button
            >
            <el-button
              type="primary"
              link
              @click="handleClickEditItem(scope.row)"
              >编辑</el-button
            >
            <el-button
              type="danger"
              link
              @click="handleClickRemoveItem(scope.row)"
              >移除</el-button
            >
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { ElMessageBox } from "element-plus";

export default {
  name: "settings-executables-table",

  computed: {
    items() {
      return this.$store.getters["executable/currentPageItems"];
    },
  },

  methods: {
    handleClickRemoveItem(item) {
      let title = "移除应用程序";
      let text = "确定要移除 [" + item.name + "] 这个应用程序吗？";
      let desc =
        "该操作不会把磁盘上安装的程序卸载或删除，仅将对它的引用从WPM数据库中移除。";
      ElMessageBox.confirm(text + desc, title, {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.removeItem(item);
        })
        .catch(() => {});
    },

    handleClickEditItem(item) {
      let name = "edit";
      let e = { name, item };
      this.$emit("action", e);
    },

    handleClickItemDetail(item) {
      let name = "detail";
      let e = { name, item };
      this.$emit("action", e);
    },

    removeItem(item) {
      let id = item.id;
      this.$store.dispatch("executable/remove", id);
    },
  },
};
</script>

<style scoped>
.operation-buttons {
  filter: opacity(1);
}

.operation-buttons:hover {
  filter: opacity(1);
}

.item-icon-img {
  width: 32px;
  height: 32px;
}
</style>
