<template>
  <div class="toolbar">
    <el-button @click="handleClickRefresh" title="刷新">
      <el-icon><Refresh /></el-icon>
    </el-button>

    <el-button @click="handleClickAdd" title="添加本地仓库">
      <el-icon><Plus /></el-icon>
    </el-button>
  </div>
</template>

<script setup>
import { Refresh, Plus } from "@element-plus/icons-vue";
</script>

<script>
export default {
  name: "settings-local-repositories-toolbar",

  data() {
    return {};
  },

  methods: {
    fetch() {
      this.$store.dispatch("executable/fetch", {});
    },

    handleClickAdd() {
      let e = { name: "add" };
      this.$emit("action", e);
    },

    handleClickRefresh() {
      this.fetch();
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>
  
<style scoped>
.toolbar {
  display: flex;
  flex-direction: row;
  justify-content: right;
}
</style>
