<template>
  <div>
    <el-pagination
      v-model:current-page="currentPage"
      v-model:page-size="pageSize"
      :page-sizes="[5, 10, 20, 50, 100, 200, 500]"
      :background="background"
      layout="total, sizes, prev, pager, next"
      :total="theCountItemsTotal"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    />
  </div>
</template>

<script>
export default {
  name: "settings-executables-pager",

  computed: {
    items() {
      return [];
    },

    theCountItemsTotal() {
      return this.$store.getters["executable/total"];
    },
  },

  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      background: false,
    };
  },

  methods: {
    handleSizeChange(value) {
      let size = value;
      this.$store.commit("executable/update", { size });
    },

    handleCurrentChange(value) {
      let index = value - 1;
      this.$store.commit("executable/update", { index });
    },

    init() {
      let p = {
        index: this.currentPage - 1,
        size: this.pageSize,
        filter: "",
      };
      this.$store.commit("executable/update", p);
    },
  },

  mounted() {
    this.init();
  },
};
</script>

