<template>
  <el-select>
    <el-option
      v-for="item in options"
      :key="item.id"
      :value="item.value"
      :label="item.text"
    ></el-option>

    <el-option :value="0" label="不使用"></el-option>
  </el-select>
</template>

<script>
export default {
  name: "open-with-priority-selector",

  data() {
    const max = 6;
    let options = [];
    for (let i = 1; i <= max; i++) {
      let item = {
        id: i,
        value: max + 2 - i,
        text: "" + i,
      };
      options.push(item);
    }
    return {
      options,
    };
  },
};
</script>
