<template>
  <div>
    <el-input
      :modelValue="searchtext"
      @input="applyFilter"
      clearable
      placeholder="请输入要查找的内容"
    >
      <template #append>
        <el-button title="查找">
          <el-icon> <Search /> </el-icon>
        </el-button>
      </template>
    </el-input>
  </div>
</template>

<script setup>
import { Search } from "@element-plus/icons-vue";
</script>

<script>
export default {
  name: "settings-executables-table",

  computed: {},

  data() {
    return {
      searchtext: "",
    };
  },

  methods: {
    applyFilter(text) {
      this.searchtext = text;
      this.$store.commit("executable/update", { filter: text });
    },
  },
};
</script>

